<template>
  <div class="container jumbotron">
    <h1 class="display-4">Fast typing event</h1>
    <p class="lead">How fast can you write</p>
    <hr class="my-4">
    <div v-if="isFinished" class="alert alert-primary">
        <h3>The Game finished</h3>
        <p class="display-4">{{this.dks}} DKS</p>
        <span>Percentage of corrects: {{truePercent}}%</span><br>
        <span>Correct: {{trueCount}}</span><br>
        <span>Wrong: {{falseCount}}</span>
        <button class="btn btn-success btn-block btn-lg mt-3" @click="newGame">NEW GAME</button>
    </div>
    <div v-else>
        <div class="card">
            <div class="card-body">
                <span v-for="(word, key) in words.filter((data, index)=>index<20)" :key="key" v-bind:class="key===0 && writingWordControl" class="ml-2 words">{{word}}</span>
            </div>
        </div>
        <div class="card">
            <div class="card-body bg-secondary">
                <div class="input-group input-group-lg">
                    <input type="text" class="form-control" v-model="writingWord">
                    <div class="input-group-append">
                        <button class="btn btn-light" disabled type="button">{{timer}} sn.</button>
                        <button :disabled="isRunning" class="btn btn-light" type="button" @click="getWords">Refresh</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
   </div>
</template>

<script>
import wordList from '@/assets/words.json'
export default {
  data () {
    return {
      words: [],
      writingWord: null,
      isTrue: true,
      trueCount: 0,
      falseCount: 0,
      timer: 60,
      interval: false,
      isRunning: false,
      isFinished: false,
      wordList: wordList
    }
  },
  watch: {
    writingWord (val) {
      if (!val || val === ' ') {
        this.writingWord = ''
        return
      }
      !this.isRunning && this.toggleTimer()
      const word = this.words[0].slice(0, val.length).toUpperCase()
      const userWord = val.replace(' ', '').toUpperCase()

      this.isTrue = word === userWord

      if (val.indexOf(' ') !== -1) {
        this.isTrue ? this.trueCount++ : this.falseCount++
        this.words.splice(0, 1)
        this.writingWord = ''
        this.isTrue = true
      }
    }
  },

  computed: {
    writingWordControl () {
      return this.isTrue ? 'writing-word' : 'writing-word bg-danger'
    },
    dks () {
      return 300 - this.words.length
    },
    truePercent () {
      const percent = (100 / this.dks)
      const val = (percent * this.trueCount).toFixed(2)
      return isNaN(val) ? 0 : val
    }
  },

  mounted () {
    this.getWords()
  },

  methods: {
    newGame () {
      this.getWords()
      this.isFinished = false
      this.timer = 60
      this.isTrue = true
      this.isRunning = false
      this.trueCount = 0
      this.falseCount = 0
    },
    getWords () {
      this.words = this.wordList.sort(() => Math.random() - 0.5).splice(0, 300)
    },
    toggleTimer () {
      this.isRunning = true
      this.interval = setInterval(this.timeProcess, 1000)
    },
    timeProcess () {
      if (this.timer === 0) {
        clearInterval(this.interval)
        this.isFinished = true
        return
      }
      this.timer--
    }
  }
}
</script>

<style>
    .words{
        font-size: 25px;
        font-weight: 400;
    }
    .writing-word{
        background-color: slategrey;
        color: white;
        padding: 5px;
        border-radius: 5px;
    }
</style>
